















































































































input, button, textarea
  border: .02rem solid #ccc
  border-radius: .06rem

input
  margin: .06rem 1.2rem .06rem .3rem
  padding: 0 .06rem
  width: 10rem

button
  padding: 0 .08rem
  margin: .2rem .2rem .1rem
  width: 2rem

.nav
  display: flex
  justify-content: center
  margin-top: .3rem

.left
  width: 15rem
  display: flex
  flex-direction: column
  justify-content: center
  align-items: flex-end

.right
  width: 5rem
  display: flex
  flex-direction: column
  justify-content: center
  align-items: flex-start

textarea
  min-width: 20rem
  min-height: 3rem
  margin: .3rem 0 .2rem 0
  white-space: nowrap
  overflow-x: scroll
  padding: .1rem .2rem

.result
  min-height: 5rem
  margin-top: .4rem

